type StackElement = {
    value: any
    next: StackElement | null
    prev: StackElement | null
}

export class Stack {
    size: number
    head: StackElement | null
    tail: StackElement | null

    constructor() {
        this.size = 0
        this.head = null
        this.tail = null
    }

    //    .
    //    .
    push(value: any): number {
        const node = { value, next: null, prev: this.tail }
        if (this.tail) {
            this.tail.next = node
        }
        this.tail = node
        if (!this.head) {
            this.head = this.tail
        }
        this.size += 1
        return this.size
    }

    //    .
    //   ,  .
    //   .
    pop(): StackElement | null {
        if (!this.head || !this.tail) {
            throw new Error(" ")
        }
        const node = this.tail
        this.tail = this.tail.prev
        if (this.tail) {
            this.tail.next = null
        } else {
            this.head = this.tail
        }
        this.size -= 1
        return node
    }

    //       .
    peek(): StackElement | null {
        return this.tail
    }

    //   ,  true.     false.
    isEmpty(): boolean {
        if (!this.head || !this.tail || this.size === 0) {
            return true
        }

        return false
    }
}