export type QueueElement = {
    value: any
    next: QueueElement | null
    prev: QueueElement | null
}

export class Queue {
    size: number
    head: QueueElement | null
    tail: QueueElement | null

    constructor() {
        this.size = 0
        this.head = null
        this.tail = null
    }

    //    
    //    
    enqueue(value: any): number {
        const node = { value, next: null, prev: this.tail }
        if (this.tail) {
            this.tail.next = node
        }
        this.tail = node
        if (!this.head) {
            this.head = this.tail
        }
        this.size += 1
        return this.size
    }

    //    .
    //   ,  .
    //   .
    dequeue(): QueueElement | null {
        if (!this.head || !this.tail || this.size === 0) {
            return null
        }
        const node = this.head
        this.head = this.head.next
        if (this.head) {
            this.head.prev = null
        } else {
            this.tail = this.head
        }
        this.size -= 1
        return node
    }

    //     .
    peek(): QueueElement | null {
        return this.head
    }

    //   ,  true.     false.
    isEmpty(): boolean {
        if (!this.head || !this.tail || this.size === 0) {
            return true
        }

        return false
    }
}